.product {
  margin: 0 60px 60px;
  background-color: #fff;
  border-radius: 6px;
  box-shadow: 0 32px 32px rgb(34 94 222 / 8%);
  position: relative;
  z-index: 1;
}
.content {
  width: var(--content-width);
  margin: -220px auto 0;
  text-align: center;
  padding: 64px 0;
  h3 {
    font-size: 28px;
    margin-bottom: 64px;
  }
}
.product-list {
  display: flex;
  justify-content: center;
  .item {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 20px;
    cursor: pointer;
    .box {
      width: 180px;
      height: 180px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border-radius: 100%;
      background: #fff;
      position: relative;
      z-index: 1;
      transition: box-shadow 0.3s cubic-bezier(0.215, 0.61, 0.355, 1),
        transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
    }
    &:nth-child(1) {
      .box {
        box-shadow: 0 16px 32px rgb(166 55 112 / 10%);
      }
    }
    &:nth-child(2) {
      .box {
        box-shadow: 0 16px 32px rgb(191 188 21 / 10%);
      }
    }
    &:nth-child(3) {
      .box {
       
        box-shadow:  0 16px 32px rgb(35 196 196 / 10%);
      }
    }
    &:nth-child(4) {
      .box {
        box-shadow: 0 16px 32px rgb(73 101 166 / 10%);
      }
    }
  }
  .name {
    font-size: 20px;
    margin-top: 32px;
  }
  .desc {
    font-size: 14px;
    color: #666;
  }
}
