.partner {
  text-align: center;
  overflow: hidden;
  background-color: hsl(0, 0%, 93%);
  padding: 60px 0 30px;
}
.content {
  width: var(--content-width);
  margin: 0 auto;
  h3 {
    font-size: 28px;
    margin-bottom: 30px;
    margin-top: 0;
  }
}
.partner-list {
  display: flex;
  flex-wrap: wrap;
  .item {
    width: calc(25% - 30px);
    padding: 15px;
    background-color: #fff;
    border-radius: 6px;
    overflow: hidden;
    margin: 15px;
  }
}