.header {
  color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  // background-color: var(--bg-color);
}
.header-content {
  width: var(--content-width);
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu {
  display: flex;
  .item {
    color: #ccc;
    padding: 0 20px;
    height: 64px;
    line-height: 64px;
    cursor: pointer;
    transition: color 0.3s ease-in-out;
    &:hover {
      color: #fff;
    }
  }
  .active {
    color: #fff;
  }
}
