.banner {
  height: 100vh;
  text-align: center;
  color: #fff;
  background: url('https://gw.alipayobjects.com/zos/rmsportal/xTPkCNNLOnTEbGgVZOpE.jpg') top center no-repeat;
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 220px;
  position: relative;
  h3 {
    font-size: 62px;
    margin: 15px;
  }
  h4 {
    font-size: 32px;
    margin: 15px;
  }
}
.btn {
  cursor: pointer;
  position: relative;
  color: #fff;
  width: 136px;
  margin: 36px auto 0;
  background: linear-gradient(to right,#034365 0%,#001b33 100%);
  box-shadow: 0 8px 16px #0a52ab;
  border: none;
  line-height: 42px;
  height: 42px;
  border-radius: 42px;
  &::after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    border-radius: inherit;
    box-shadow: 0 0 0 0 #2f54eb;
    opacity: .2;
    content: '';
    pointer-events: none
  }
}